<template>
    <div id="app">
        <div class="window_con">
            <div class="title">
                手写板管理
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
            <div class="userForm">
                <div class="inputItem">
                    <div class="label">姓名</div>
                    <input type="text" class="disabled" v-model="name" disabled />
                </div>
                <div class="inputItem">
                    <div class="label">性别</div>
                    <input type="text" class="disabled" v-model="gender" disabled />
                </div>
                <div class="inputItem">
                    <div class="label">学号</div>
                    <input type="text" class="disabled" v-model="stuNum" disabled />
                </div>
                <div class="inputItem">
                    <div class="label">手写板设备号</div>
                    <input type="text" class="mac" v-model="stuMac" ref="macInput"/>
                    <div class="btnClear" @click="clearMac"><img src="@/assets/icon_clear@2x.png" /></div>
                </div>
                <div class="footer">
                    <div class="btnCancel" @click="closeWindow">取消</div>
                    <div class="btnConfirm" @click="submitMac" :class="{disabled:!canUse}">确定</div>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
    var originMac = utils.getUrlPrams("stuMac")
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    export default {
        name: 'App',
        data() {
            return {
                dpr: 1,
                name: "",
                gender: "",
                stuId: "",
                stuMac: "",
                stuNum: ""
            }
        },
        mounted() {
            this.dpr = localStorage.getItem("width") / 1920
            //获取url参数
            this.name = utils.getUrlPrams("name")
            this.gender = utils.getUrlPrams("sex") == 1 ? "男" : "女"
            this.stuId = utils.getUrlPrams("stuId")
            this.stuMac = utils.getUrlPrams("stuMac")
            this.stuNum = utils.getUrlPrams("stuNum")

        },
        computed: {
          canUse(){
              this.stuMac = this.stuMac.toUpperCase()
              return this.stuMac != ""&&this.stuMac.length==12&&this.stuMac!==originMac
          }  
        },
        methods: {
            submitMac() {
                var that = this
                let stuId = this.stuId
                let stuMac = this.stuMac
                console.log(stuMac.length)
                if (this.canUse) {
                    axios.get(API.api_tl + 'api/updateDeviceMac', {
                        params: {
                            stuId: stuId,
                            deviceMac: stuMac,
                            source: 3,
                        }
                    }).then(function (res) {
                        if (res.data.code == 0) {
                            console.log("修改成功")
                            window.cefQuery({
                                request: "isScuess:true&" + stuId + "&" + stuMac,
                                onsuccess: function (response) {
                                    console.log("invoke success response = " + response);
                                },
                                onFailure: function (ecode, emsg) {
                                    console.log("invoke fail code = " + ecode + " msg:" + emsg);
                                }
                            });
                        }else{
                            window.cefQuery({
                                request: "isScuess:false&"+res.data.msg,
                                onsuccess: function (response) {
                                    console.log("invoke success response = " + response);
                                },
                                onFailure: function (ecode, emsg) {
                                    console.log("invoke fail code = " + ecode + " msg:" + emsg);
                                }
                            });
                        }

                    }).catch(function (error) {
                        console.log(error);
                        window.cefQuery({
                                request: "isScuess:false&网络错误，请检查你的网络重试",
                                onsuccess: function (response) {
                                    console.log("invoke success response = " + response);
                                },
                                onFailure: function (ecode, emsg) {
                                    console.log("invoke fail code = " + ecode + " msg:" + emsg);
                                }
                            });
                    });
                    window.cefQuery({
                    request: "updateDeviceMac:" + stuId + "&" + stuMac,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                            console.log("invoke fail code = " + ecode + " msg:" + emsg);
                        }
                    });
                }
            },
            clearMac() {
                this.stuMac = ""
                this.$refs.macInput.focus()
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowUpdateUserInfo",   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            locadBaseInfo(res) {

                var that = this
                axios.get(API.api_tl + 'api/selClassStus', {
                    params: {
                        classId: that.classId
                    }
                }).then(function (res) {
                    console.log(res.data.data)
                    that.stuList = res.data.data

                }).catch(function (error) {
                    console.log(error);
                });
            },
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #333;
        width: 100%;
        height: 100%;
    }

    .el-loading-spinner .path {
        stroke: #8BADCE !important;
    }
</style>
<style scoped>
    .window_con {
        box-sizing: border-box;
        width: 100%;
        height: 100vh;
        /* padding-bottom: 0; */
        border-radius: 0.2rem;
        background: #fff;
        display: flex;
        flex-direction: column;
    }

    .userForm {
        box-sizing: border-box;
        height: calc(100vh - 0.1rem);
        padding: 0 0.2rem 0.1rem 0.2rem;
        position: relative;
    }

    .footer {
        box-sizing: border-box;
        width: 100%;
        ;
        position: absolute;
        bottom: 0.1rem;
        right: 0.2rem;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        justify-content: flex-end;
    }

    .inputItem {
        position: relative;
    }

    .inputItem .label {
        position: absolute;
        left: 0;
        top: 0;
        font-size: 0.16rem;
        line-height: 0.47rem;
        color: #2E4A66;
    }

    .userForm input {
        box-sizing: border-box;
        width: 100%;
        height: 0.47rem;
        font-size: 0.16rem;
        text-align: right;
        line-height: 1;
        color: #2E4A66;
        background: none;
        outline: none;
        border: none;
        border-bottom: 1px solid #C0C8D1;
    }

    .userForm input.disabled {
        color: #96A4B2;
    }

    .userForm input.mac {
        padding-right: 0.4rem;
        padding-left: 1rem;
    }

    .btnConfirm.disabled{
        opacity: 0.5;
    }

    .btnClear {
        height: 0.47rem;
        line-height: 0.47rem;
        position: absolute;
        right: 0;
        top: 0;
        display: flex;
        align-items: center;
    }

    .btnClear img {
        display: block;
        width: 0.16rem;
        height: 0.16rem;
    }

    .btnCancel {
        box-sizing: border-box;
        width: 0.82rem;
        height: 0.36rem;
        font-size: 0.16rem;
        text-align: center;
        line-height: 0.36rem;
        color: #96A4B2;
        border-radius: 0.03rem;
        border: 0.01rem solid rgba(139, 173, 206, 1);
    }

    .btnConfirm {
        box-sizing: border-box;
        width: 0.82rem;
        height: 0.36rem;
        font-size: 0.16rem;
        text-align: center;
        line-height: 0.36rem;
        background: #9DBEDA;
        color: #fff;
        border-radius: 0.03rem;
        box-shadow: 0px 0.01rem 0.02rem 0px rgba(156, 200, 235, 0.2);
        margin-left: 0.2rem;
    }
    @media screen and (max-width: 220px) {
        .btnClear img {
            display: block;
            width: 0.3rem;
            height: 0.3rem;
        }
    }
</style>